.card {
    width: 350px;
    padding: 1.5rem;
    background-color: #fff;
    margin: 1rem;
    position: relative;
    border-radius: .3rem;
    z-index: 1;
}

.record {
    top: 3rem;
}

.brushes {
    top: 6rem;
}


.card-icon {
    background-color: #3B3054;
    border-radius: 50%;
    display: inline-block;
    padding: 1.5rem;
    position: relative;
    top: -4rem;
    margin: 0;
    height: 88px;
    width: 88px;
}

.card-icon img {
    width: 40px;
}

.card-content {
    margin-top: -2.5rem;
    line-height: 1.5rem;
}

.card-content h3 {
    margin: 0;
    padding: 0;
    letter-spacing: -.03rem;
    color: hsl(260, 8%, 14%);
}

.card-content p {
    font-size: 1.05rem;
}